<template>
    <div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="GlobalAwardSet" label-width="200px">

                    <el-form-item label="注册数量条件">
                        <el-input-number class="loose-input" v-model="GlobalAwardSet.RegCount" :step="1" :min="1" ></el-input-number>
                    </el-form-item>
                    <el-form-item label="购买金额条件">
                        <el-input-number class="loose-input" v-model="GlobalAwardSet.BuyFilmGold" :step="100" :min="1" ></el-input-number>
                    </el-form-item>
                    <el-form-item label="奖励最小金额">
                        <el-input-number class="loose-input" v-model="GlobalAwardSet.AwardMin" :step="10" :min="1" ></el-input-number>
                    </el-form-item>
                    <el-form-item label="奖励最大金额">
                        <el-input-number class="loose-input" v-model="GlobalAwardSet.AwardMax" :step="10" :min="1" ></el-input-number>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">表单提交</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
    import { request } from '../../utils/http';

    export default {
        name: 'GlobalAwardSet',
        data() {
            return {
                GlobalAwardSet:{},
                query: {
                    id:0
                },

            };
        },
        methods: {
            onSubmit() {
                request({url: 'filmbll/updateglobalaward', method:'post', data:this.GlobalAwardSet }).then((res)=>{
                    if (res.code==200){
                        this.$message.success('提交成功！');
                    }
                });
            } ,
            getData() {
                request({ url:'filmbll/getglobalaward', method:'post',data: "" }).then((res)=>{
                    if (res.code==200){
                        console.log(res.obj)
                        this.GlobalAwardSet = res.obj;
                    }
                });
            },
        },
        created() {
            this.getData();
        },mounted() {

        }
    };
</script>

